/** */

function obtainDataMarkets(latitud1, latitud2, longitud1, longitud2, date1, date2, tanque_nivel, tanque_litros) {

  let markers = [];

  let data = {
    locomotora: "Locomotora Test 1",
    velocidad: CalculateSpeed(latitud1, latitud2, longitud1, longitud2, date1, date2) + " K/H",
    // kilometraje: "200 000",
    // motor: "Encendido",
    // direccion: "Preguntar que es",
    // engranaje: "4",
    // voltajes: "120 v",
    n_combustible: tanque_nivel,
    lts_combustible: tanque_litros,
    fecha: _today,
    HorasTrabajo: 'NA',
    position: {
      lat: latitud1,
      lng: longitud1
    },
  }

  markers.push(data);

  return markers;
}

