let registrarNotificacion = (idMachine, errorMessagge, tipoNotification) => {

  // messageUser, messageError, tipoNotification
  let dataToSend = {
    idMachine: idMachine,
    messageResume: errorMessagge.statusCode + " - " + errorMessagge.statusText,
    tipoNotification: tipoNotification
  };

  $.ajax({
    url: urlApi + '/guardarRegistroNotificacion',
    type: 'POST',
    async: false,
    data: JSON.stringify(dataToSend),
    success: function (response) {

      showAlarms('2', 'nc-icon nc-notification-70', "Se guardo el registro exitosamente");
    },
    error: function (ex) {
      console.error(ex);
      showAlarms('4', 'nc-icon nc-notification-70', "Ocurrio un error al obtener la información", ex.statusCode + " - " + ex.statusText);
    }
  });
}

